import React, { useState, useEffect } from "react";
import { getCurrentWeather, getHistoricalWeather } from "./services/weatherService";
import WeatherChart from "./components/WeatherChart.jsx"; // <<< Corrected import path

function App() {
  const [city, setCity] = useState("");
  const [currentWeather, setCurrentWeather] = useState(null);
  const [historicalData, setHistoricalData] = useState([]);
  const [loading, setLoading] = useState(false);
  // The error message display was removed per your request in previous turns
  // const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      if (!city) {
        // setError(null);
        setCurrentWeather(null);
        setHistoricalData([]);
        return;
      }

      setLoading(true);
      // setError(null);
      setCurrentWeather(null);
      setHistoricalData([]);

      try {
        const current = await getCurrentWeather(city);
        setCurrentWeather(current);

        // Fetch historical data using coordinates from current weather
        const historical = await getHistoricalWeather(current.coord.lat, current.coord.lon);
        setHistoricalData(historical);
      } catch (err) {
        console.error("Error fetching weather data:", err);
        // setError("Could not retrieve weather data for that city. Please try again.");
      } finally {
        setLoading(false);
      }
    };

    // Debounce API calls to prevent excessive requests while typing
    const debounceFetch = setTimeout(() => {
      fetchData();
    }, 700); // Wait 700ms after last keypress

    // Cleanup function to clear the timeout if city changes before debounce completes
    return () => clearTimeout(debounceFetch);
  }, [city]); // Re-run effect whenever 'city' state changes

  return (
    <div style={appContainerStyle}>
      <h1 style={headingStyle}>🌌 Weather Forecast 🌌</h1>
      <div style={inputContainerStyle}>
        <input
          type="text"
          value={city}
          onChange={(e) => setCity(e.target.value)}
          placeholder="Enter city name..."
          style={inputStyle}
        />
      </div>

      {loading && <p style={loadingMessageStyle}>Fetching atmospheric data...</p>}
      {/* The error message display was removed per your request */}
      {/* {error && <p style={errorMessageStyle}>⚠️ {error}</p>} */}

      {/* Welcome message when no city is entered and not loading */}
      {!city && !loading && (
        <p style={welcomeMessageStyle}>
          Input a city to unveil its current and historical climate patterns.
        </p>
      )}

      {/* Display current weather details if available */}
      {currentWeather && (
        <div style={currentWeatherCardStyle}>
          <h2 style={cardTitleStyle}>Current Conditions in {currentWeather.name}</h2>
          <p style={cardTextStyle}>
            Temperature: <span style={highlightTextStyle}>{currentWeather.main.temp}°C</span>
          </p>
          <p style={cardTextStyle}>
            Feels like: {currentWeather.main.feels_like}°C
          </p>
          <p style={cardTextStyle}>
            Humidity: {currentWeather.main.humidity}% | Pressure: {currentWeather.main.pressure} hPa
          </p>
          <p style={cardTextStyle}>
            Wind Speed: {currentWeather.wind.speed} m/s | Direction: {currentWeather.wind.deg}°
          </p>
          <p style={cardTextStyle}>
            Cloudiness: {currentWeather.clouds.all}%
          </p>
          <p style={cardConditionStyle}>
            Atmospheric State: {currentWeather.weather[0].description}
          </p>
        </div>
      )}

      {/* Render the WeatherChart if historicalData is available */}
      {historicalData.length > 0 && (
        <div style={historicalDataCardStyle}>
          <h2 style={cardTitleStyle}>Historical Climate Readings (Last 7 Days)</h2>
          {/* Passing the fetched historicalData to the WeatherChart component */}
          <WeatherChart weatherData={historicalData} />
        </div>
      )}
    </div>
  );
}

