import React from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

// Register Chart.js components - This is CRUCIAL for Chart.js to work
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const WeatherChart = ({ weatherData }) => {
  // Display a message if no historical data is provided
  if (!weatherData || weatherData.length === 0) {
    return <p style={noDataMessageStyle}>No historical climate data available for this sector.</p>;
  }

  // Prepare data for the chart: Extract dates for labels and temperatures for data points
  const dates = weatherData.map(data =>
    new Date(data.dt * 1000).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    })
  );
  const temperatures = weatherData.map(data => data.main.temp);

  // Define the data structure for the Chart.js Line chart
  const data = {
    labels: dates, // X-axis labels (dates)
    datasets: [
      {
        label: 'Temperature Anomaly (°C)', // Label for the dataset
        data: temperatures, // Y-axis data (temperatures)
        fill: true, // Fill the area under the line
        backgroundColor: 'rgba(0, 255, 255, 0.1)', // Color of the filled area (semi-transparent neon cyan)
        borderColor: '#00e6e6', // Color of the line (neon cyan)
        borderWidth: 3,
        tension: 0.4, // Smoothness of the line curve
        pointRadius: 5, // Size of data points
        pointBackgroundColor: '#ff66b2', // Color of data points (cyber-pink)
        pointBorderColor: '#0d1a2a', // Border color of data points (deep background color)
        pointBorderWidth: 2,
        pointHoverRadius: 8, // Size of data points on hover
        pointHoverBackgroundColor: '#00ffff', // Color of data points on hover (brighter cyan)
        pointHoverBorderColor: '#fff', // Border color of data points on hover
      },
    ],
  };

  // Define options for the Chart.js Line chart (titles, tooltips, scales, etc.)
  const options = {
    responsive: true, // Chart will resize with its container
    maintainAspectRatio: false, // Allows chart to fill container height without maintaining aspect ratio
    plugins: {
      legend: {
        position: 'top', // Position of the legend
        labels: {
          font: {
            size: 16,
            family: "'Orbitron', sans-serif", // Font for legend labels
          },
          color: '#e0f7fa', // Color of legend labels
        },
      },
      title: {
        display: true,
        text: 'Daily Atmospheric Temperature Readouts', // Chart title
        font: {
          size: 22,
          weight: 'bold',
          family: "'Orbitron', sans-serif", // Font for chart title
        },
        color: '#00e6e6', // Color of chart title
        padding: {
            top: 10,
            bottom: 20
        },
        textShadow: '0 0 10px rgba(0, 255, 255, 0.5)', // Glow effect for title
      },
      tooltip: {
        backgroundColor: 'rgba(0, 0, 0, 0.8)', // Background color of tooltip
        borderColor: '#00e6e6', // Border color of tooltip
        borderWidth: 1,
        bodyFont: {
          size: 15,
          family: "'Orbitron', sans-serif", // Font for tooltip body
        },
        titleFont: {
          size: 17,
          weight: 'bold',
          family: "'Orbitron', sans-serif", // Font for tooltip title
        },
        displayColors: false, // Don't show color box in tooltip
        padding: 12,
        cornerRadius: 6,
      },
    },
    scales: {
      x: {
        title: {
          display: true,
          text: 'Chronological Mark', // X-axis title
          color: '#80deea',
          font: {
            size: 16,
            weight: 'bold',
            family: "'Orbitron', sans-serif",
          },
        },
        grid: {
          color: 'rgba(0, 255, 255, 0.1)', // Color of x-axis grid lines (fainter, glowing)
          drawBorder: false, // Don't draw border on x-axis
        },
        ticks: {
          color: '#b3e5fc', // Color of x-axis tick labels
          font: {
            family: "'Orbitron', sans-serif",
          },
        },
      },
      y: {
        title: {
          display: true,
          text: 'Temperature (°C)', // Y-axis title
          color: '#80deea',
          font: {
            size: 16,
            weight: 'bold',
            family: "'Orbitron', sans-serif",
          },
        },
        grid: {
          color: 'rgba(0, 255, 255, 0.1)', // Color of y-axis grid lines (fainter, glowing)
          drawBorder: false, // Don't draw border on y-axis
        },
        ticks: {
          color: '#b3e5fc', // Color of y-axis tick labels
          font: {
            family: "'Orbitron', sans-serif",
          },
        },
        beginAtZero: false, // Y-axis doesn't start at zero as temperatures can be negative
      },
    },
  };

  return (
    <div style={chartContainerStyle}>
      <Line data={data} options={options} />
    </div>
  );
};

// --- Futuristic Styles for WeatherChart.jsx (these are placed here for completeness) ---

const chartContainerStyle = {
  position: 'relative',
  width: '100%',
  height: '400px',
  marginTop: '25px',
  marginBottom: '15px',
  boxShadow: '0 0 20px rgba(0, 255, 255, 0.1) inset',
  borderRadius: '10px',
  padding: '10px',
};

const noDataMessageStyle = {
  color: '#ff66b2',
  fontSize: '1.4em',
  marginTop: '25px',
  fontWeight: 'bold',
  textShadow: '0 0 5px rgba(255, 102, 178, 0.5)',
};

export default WeatherChart;